<template>
<div id="components-layout-demo-basic">
  <a-layout>
    <a-layout-header>
      <img src="data:image/png;base64,"/>
      <span>全民 Party 豬頭王 🐷 </span>
      </a-layout-header>
    <a-layout-content>
      <a-alert v-if="this.party_rooms.length > 0" style="margin-bottom: 5px;" type="success">
        <span slot="description">匯入資料：{{import_date}} <a-button :href="this.orig_link">重新匯入</a-button></span>
        <!-- <span slot="message">台北時間 {{ motz().tz("Asia/Taipei").format("YYYY-MM-DD HH:mm:ss")}} </span> -->
        </a-alert>

      <a-row v-if="admin">
        <a-col :span="24">
          <a-date-picker :default-value="today" @change="onChange"/>
        </a-col>
      </a-row>

      <a-row v-if="admin" type="flex" justify="space-around" align="middle">

        <a-col :span="12">
          <span>時間筆數：{{ times_length }}</span>
          <a-textarea v-model="times" placeholder="請貼上房間時間" :rows="10" />
        </a-col>
        <a-col :span="12">
          <span>房間筆數： {{ rooms_length }}</span>
          <a-textarea v-model="rooms" placeholder="請貼上房間號碼" :rows="10" />
        </a-col>
      </a-row>
      <a-row v-if="admin">
        <a-textarea v-model="metadata_string" :rows="10" />
      </a-row>
      
      <a-button v-if="admin" @click="save_rooms" type="primary" block>
        產生
      </a-button>

      <a-textarea v-if="admin" v-model="link" :auto-size="{ minRows: 2, maxRows: 10 }" />
      <!-- <a-button @click="recover_room" type="dashed" style="color: red" block>復原房號 {{this.last_room}} </a-button>
      <br />
      <br /> -->
    <!-- <p v-if="this.party_rooms.length > 0">還剩下幾隻豬仔🐷：{{this.party_rooms.length}}</p> -->
    <div v-if="this.party_rooms.length > 0" style="background: #ececec">
      <a-row>
        <a-col :span="12">
          <a-card>
            <a-statistic title="剩下豬仔🐷" :value="this.party_rooms.length">
            <template #suffix>
              / {{ total_rooms }}
            </template>
            </a-statistic>

          </a-card>
        </a-col>
        <a-col :span="12">
          <a-card>
            <a-statistic title="抓幾隻🐷" :value="this.pickup"/>
          </a-card>
        </a-col>
      </a-row>
      
    </div>
          <div v-if="!admin"><a-alert v-if="this.party_rooms.length == 0" message="請透過每日專屬網址匯入房間資料喔 🙃" type="error" show-icon/></div>
          
          <a-alert v-if="this.party_rooms.length > 0" style="margin-bottom: 0px; margin-top: 10px;" type="info" show-icon>
          <span slot="message">{{this_hour_count}} 重新整理 </span>
          <span slot="description">下一小時：{{next_hour_count}} </span>
          
          </a-alert>
            
    <a-button style="margin-top: 10px;" @click="clear_past_rooms" v-if="party_rooms.length > 0" type="danger" block>
      清除過期房間(保留上一小時)
    </a-button>
    <a-list style="margin-top: 10px; background: white;"  bordered :data-source="party_rooms">
      <a-list-item slot="renderItem" slot-scope="item, index">
        <a slot="actions"><a-button @click="handle_link(index, item.room)">🐷 GO</a-button></a>
        <a slot="actions"><a-button @click="handle_close(index)">❌</a-button></a>
        <a-list-item-meta :description="item.time">
        <a slot="title">{{ item.room }} </a>
        
        <a-avatar slot="avatar">🚪 </a-avatar>
        </a-list-item-meta>
        
      </a-list-item>
    </a-list>
    <a-button style="margin-top: 10px;" @click="clear_rooms" v-if="party_rooms.length > 0" type="danger" block>
        全部清除
      </a-button>

      <p>
        <a href="https://hits.seeyoufarm.com"><img src="https://hits.seeyoufarm.com/api/count/incr/badge.svg?url=https%3A%2F%2Fparty-room.github.io&count_bg=%2379C83D&title_bg=%23555555&icon=iconify.svg&icon_color=%23F7F7F7&title=hits&edge_flat=false"/></a>
      </p>

    </a-layout-content>
    <a-layout-footer>Copyright © 2021 全民Party Möᴛ音樂至上⭑阿里柏𝄞ᐝ</a-layout-footer>
  </a-layout>  
</div>

</template>

<script>

// import moment from 'moment';
import moment from 'moment';
import motz from 'moment-timezone';

export default {
  data() {
    return {
      "party_icon": 'data:image/png;base64,',
      "rooms": '',
      "admin": false,
      "rooms_array": [],
      "times": '',
      "times_array": [],
      "party_rooms": [],

      "link": "",
      "orig_link": "",
      "pickup": 0,
      today: moment(),
      "metadata_string": "",
      "metadata": {"date": this.today, "total":0,  }
      // "now_hour":10,
      // "last_room": 100188,
    }
  },
  methods: {
    motz,
    moment,
    onChange(date, dateString){
      console.log(date,dateString)
      this.metadata.date = dateString
    },
    save_rooms(){
      this.party_rooms = []
      this.rooms_array = this.rooms.split("\n").filter(Number)
      this.times_array = this.times.split("\n").filter(word => word.length > 0)

      for (let i = 0; i < this.rooms_array.length; i++) {
        this.party_rooms.push(
          {
            "time": this.times_array[i],
            "room": this.rooms_array[i],
          }
        )
      }

      let str_party = JSON.stringify(this.party_rooms)
      // console.log(str_party)
      this.metadata_string = str_party
      this.link = window.location.href + '?b=' + window.btoa(str_party)
      this.orig_link = this.link
      localStorage.setItem('party_rooms', str_party)


    },
    clear_rooms(){
      this.times = ""
      this.rooms = ""
      this.party_rooms = []
      this.link = ""
      localStorage.removeItem('party_rooms')
    },
    clear_past_rooms(){
      let this_hour = motz().tz("Asia/Taipei").hours()
      let new_rooms = []
      this.party_rooms.forEach( (room) => {
        if (this_hour <= moment(room.time,"HH:mm").hours()+1){
          new_rooms.push(room)
          // console.log(room.room, room.time)
        }
      })
      if (new_rooms.length > 1) {
        this.party_rooms = new_rooms
      }
      
    },
    handle_link(index, no){
      let this_hour = motz().tz("Asia/Taipei").hours()
      let this_room_hour = moment(this.party_rooms[index].time,"HH:mm").hour()
      if (this_room_hour > this_hour) {
        alert("目前房間時間還沒到!")
      }else{
        this.party_rooms.splice(index, 1);
        localStorage.setItem('party_rooms', JSON.stringify(this.party_rooms))
        this.pickup = this.pickup + 1;
        localStorage.setItem('pickup', this.pickup)
        window.open("https://party.haochang.tv/share/rooms?inviteCode=&roomCode="+ no +"&language=zh-TW&roomId=92190&channel=line&userId=23368648","subwin")
      }
      // window.location.href = "https://party.haochang.tv/share/rooms?inviteCode=&roomCode="+ no +"&language=zh-TW&roomId=92190&channel=line&userId=23368648";
    },
    handle_close(index){
      let close_room = this.party_rooms[index]
      console.log(close_room)
      localStorage.setItem('last_room', JSON.stringify(close_room))
      this.party_rooms.splice(index, 1);
      
      localStorage.setItem('party_rooms', JSON.stringify(this.party_rooms))
      this.link = window.location.origin + '/?b=' + window.btoa(JSON.stringify(this.party_rooms))
    },
    showConfirm() {
      this.$confirm({
        title: 'Do you Want to delete these items?',
        content: 'hh',
        onOk() {
          console.log('OK');
        },
        onCancel() {
          console.log('Cancel');
        },
        class: 'test',
      });
    },
  },


  mounted() {

    // this.metadata = JSON.stringify(this.metadata)
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    
    if(urlParams.get('admin') == 1) {
      this.admin = true
    }

    if (urlParams.has('b')) {
      let b64 = urlParams.get('b')
      let str_party_rooms = window.atob(b64)
      // console.log(str_party_rooms)

      let hs = Array()
      let as = Object()
      JSON.parse(str_party_rooms).forEach( ele => {
        let h = moment(ele.time,"hh:mm").hour()
        hs.push(h)
        })
      for (let i=0;i<24;i++) {
        let v = hs.filter( z=> z==i).length
        as[i] = v 
      }

      this.metadata.total = JSON.parse(str_party_rooms).length
      this.metadata.date = moment().format("YYYY-MM-DD")
      this.metadata.hours_count = as

      localStorage.setItem('metadata', JSON.stringify(this.metadata))
      localStorage.setItem('party_rooms', str_party_rooms)
      localStorage.setItem('pickup', 0)
    }
    
    if (localStorage.party_rooms) {

      this.now_hour = moment().hour()
      this.party_rooms = JSON.parse(localStorage.party_rooms)
      this.link = window.location.origin + '/?b=' + window.btoa(JSON.stringify(this.party_rooms))
      this.pickup = Number(localStorage.pickup)
    } 

    window.history.pushState("", "", '/');

  },
  computed:{

    import_date() {
      return JSON.parse(localStorage.metadata).date
    },

    times_length(){
      return this.times.split("\n").filter(word => word.length > 0).length
    },

    rooms_length(){
      return this.rooms.split("\n").filter(Number).length
    },

    total_rooms() {
      return JSON.parse(localStorage.metadata).total
    },

    today_message() {
      let today = JSON.parse(localStorage.metadata).date
      return today
    },

    this_hour_count() {
      let hours_count = JSON.parse(localStorage.metadata).hours_count
      let this_hour = motz().tz("Asia/Taipei").hours()
      let next_hour = moment().hours(this_hour+1).hours()
      return "本時段("+this_hour+"~" + next_hour +")豬頭數量：" + hours_count[this_hour]
    },
    
    next_hour_count() {
      let hours_count = JSON.parse(localStorage.metadata).hours_count
      let this_hour = motz().tz("Asia/Taipei").hours()
      let next_hour = moment().hours(this_hour+1).hours()
      return hours_count[next_hour]
    }


    // hours_compute: () => {
    //   let time_pos = Array(this.times.split("\n").filter(word => word.length > 0))
    //   time_pos.forEach(ele => ` `)
    // }

  }
}
</script>

